    //     { name: 'name', placeholder: '', required: true, object: 'input', type: 'text' },
    //     { name: 'surname', placeholder: '', required: true, object: 'input', type: 'text' },
    //     { name: 'town', placeholder: '', required: true, object: 'input', type: 'text' },
    //     { name: 'phone', placeholder: ' ', required: true, object: 'input', type: 'tel' },
    //     { name: 'email', placeholder: 'Email', required: true, object: 'input', type: 'email' },
    //     {
    //         name: 'payOptions',
    //         object: 'select',
    //         options: [
    //             '    ',
    //             '     ',
    //             '    ( )',
    //             '    ( )',
    //             '     (  )'
    //         ]
    //     },
